.button-toggle {
  margin-bottom: 40px;
}

.button-toggle__icon {
  background-color: transparent;
  border: none;
  padding: 0;

  @include media-width($tablet-width) {
    margin-right: 200px;
    margin-bottom: 24px;
    margin-top: 24px;
  }

  @include media-width($desktop-width) {
    margin-right: 300px;
  }
}

.button-toggle__wrapper {
  @include media-width($tablet-width) {
    border-bottom: 1px solid $dark;
  }
}
